import {
  Expression,
  FieldExpression,
  FieldExpressionImpl,
  TextExpression,
  TextExpressionImpl,
  NumericExpression,
  NumericExpressionImpl,
  AddExpression,
  AddExpressionImpl,
  SubtractExpression,
  SubtractExpressionImpl,
  MultiplyExpression,
  MultiplyExpressionImpl,
  DivideExpression,
  DivideExpressionImpl,
  SumExpression,
  SumExpressionImpl,
  AbsExpression,
  AbsExpressionImpl,
  ModExpression,
  ModExpressionImpl,
  FloorExpression,
  FloorExpressionImpl,
  CeilExpression,
  CeilExpressionImpl,
  ConcatExpression,
  ConcatExpressionImpl,
  StringifyExpression,
  StringifyExpressionImpl,
  ToLowerExpression,
  ToLowerExpressionImpl,
  ToUpperExpression,
  ToUpperExpressionImpl,
  SubstringExpression,
  SubstringExpressionImpl,
  LengthExpression,
  LengthExpressionImpl,
} from './Expression'

export const expressions: WixDataAggregatePipelineExpressions = {
  field: (fieldPath: string): FieldExpression =>
    new FieldExpressionImpl(fieldPath),
  text: (value: string): TextExpression => new TextExpressionImpl(value),
  numeric: (value: number): NumericExpression =>
    new NumericExpressionImpl(value),
  add: (...expressions: Expression[]): AddExpression =>
    new AddExpressionImpl(expressions),
  subtract: (first: Expression, second: Expression): SubtractExpression =>
    new SubtractExpressionImpl(first, second),
  multiply: (...expressions: Expression[]): MultiplyExpression =>
    new MultiplyExpressionImpl(expressions),
  divide: (first: Expression, second: Expression): DivideExpression =>
    new DivideExpressionImpl(first, second),
  sum: (...expressions: Expression[]): SumExpression =>
    new SumExpressionImpl(expressions),
  abs: (expression: Expression): AbsExpression =>
    new AbsExpressionImpl(expression),
  mod: (first: Expression, second: Expression): ModExpression =>
    new ModExpressionImpl(first, second),

  floor: (expression: Expression): FloorExpression =>
    new FloorExpressionImpl(expression),
  ceil: (expression: Expression): CeilExpression =>
    new CeilExpressionImpl(expression),
  concat: (...expressions: Expression[]): ConcatExpression =>
    new ConcatExpressionImpl(expressions),
  stringify: (expression: Expression): StringifyExpression =>
    new StringifyExpressionImpl(expression),
  toLower: (expression: Expression): ToLowerExpression =>
    new ToLowerExpressionImpl(expression),
  toUpper: (expression: Expression): ToUpperExpression =>
    new ToUpperExpressionImpl(expression),
  substring: (
    expression: Expression,
    start: Expression,
    length?: Expression
  ): SubstringExpression =>
    new SubstringExpressionImpl(expression, start, length),
  length: (expression: Expression): LengthExpression =>
    new LengthExpressionImpl(expression),
}

/**
 * @builder
 */
export interface WixDataAggregatePipelineExpressions {
  /**
   * Specifies which field to resolve.
   *
   * The `field()` method configures the aggregation pipeline to resolve the value of the specified field as an expression.
   *
   * @public
   * @documentationMaturity preview
   * @param fieldPath - Path to the field containing the value to resolve. Use dot notation to specify nested fields. For example, `user.name` or `product.price`.
   * @returns FieldExpression object.
   */
  field(fieldPath: string): FieldExpression
  /**
   * String.
   *
   * The `text()` method configures the aggregation pipeline to run the aggregation on the specified string.
   *
   * @public
   * @documentationMaturity preview
   * @param value - String to run the aggregation on.
   * @returns TextExpression object.
   */
  text(value: string): TextExpression
  /**
   * Number.
   *
   * The `numeric()` method configures the aggregation pipeline to run the aggregation on the specified number.
   *
   * @public
   * @documentationMaturity preview
   * @param value - Number to run the aggregation on.
   * @returns NumericExpression object.
   */
  numeric(value: number): NumericExpression
  /**
   * Adds the specified expressions.
   *
   * The `add()` method configures the aggregation pipeline to add the values of the specified expressions.
   *
   * @public
   * @documentationMaturity preview
   * @param expressions - Expressions to add together. All expressions must resolve to numbers. Learn more about using [expressions in aggregation pipelines](https://dev.wix.com/docs/sdk/backend-modules/data/items/wix-data-aggregate-pipeline-expressions/introduction).
   * @returns AddExpression object.
   */
  add(...expressions: Expression[]): AddExpression
  /**
   * Subtracts 1 expression from another.
   *
   * The `subtract()` method configures the aggregation pipeline to subtract the second expression from the first expression.
   *
   * @public
   * @documentationMaturity preview
   * @param first - Expression to subtract `second` from. Expression must resolve to a number.
   * @param second - Expression to subtract from `first`. Expression must resolve to a number.
   * @returns SubtractExpression object.
   */
  subtract(first: Expression, second: Expression): SubtractExpression
  /**
   * Multiplies multiple expressions together.
   *
   * The `multiply()` method configures the aggregation pipeline to multiply the specified expressions.
   *
   * @public
   * @documentationMaturity preview
   * @param expressions - Expressions to multiply. All expressions must resolve to numbers. Learn more about using [expressions in aggregation pipelines](https://dev.wix.com/docs/sdk/backend-modules/data/items/wix-data-aggregate-pipeline-expressions/introduction).
   * @returns MultiplyExpression object.
   */
  multiply(...expressions: Expression[]): MultiplyExpression
  /**
   * Divides 1 expression by another.
   *
   * The `divide()` method configures the aggregation pipeline to divide the first expression by the second expression.
   *
   * @public
   * @documentationMaturity preview
   * @param first - Expression to divide by `second`. Expression must resolve to a number.
   * @param second - Expression to divide `first` by. Expression must resolve to a non-zero number.
   * @returns DivideExpression object.
   */
  divide(first: Expression, second: Expression): DivideExpression
  /**
   * Calculates the total sum of multiple expressions.
   *
   * The `sum()` method configures the aggregation pipeline to calculate the total sum of the specified expressions.
   *
   * @public
   * @documentationMaturity preview
   * @param expressions - Expressions to calculate the total sum of. All expressions must resolve to numbers. Learn more about using [expressions in aggregation pipelines](https://dev.wix.com/docs/sdk/backend-modules/data/items/wix-data-aggregate-pipeline-expressions/introduction).
   * @returns SumExpression object.
   */
  sum(...expressions: Expression[]): SumExpression
  /**
   * Finds the absolute value of an expression.
   *
   * The `abs()` method configures the aggregation pipeline to find the absolute value of the specified expression.
   *
   * @public
   * @documentationMaturity preview
   * @param expression - Expression to find the absolute value of. Expression must resolve to a number. Learn more about using [expressions in aggregation pipelines](https://dev.wix.com/docs/sdk/backend-modules/data/items/wix-data-aggregate-pipeline-expressions/introduction).
   * @returns AbsExpression object.
   */
  abs(expression: Expression): AbsExpression
  /**
   * Finds the remainder when dividing 1 expression by another.
   *
   * The `mod()` method configures the aggregation pipeline to find the remainder when dividing the first expression by the second expression.
   *
   * @public
   * @documentationMaturity preview
   * @param first - Expression to divide by `second` to find the remainder. Expression must resolve to a number.
   * @param second - Expression to divide `first` by. Expression must resolve to a non-zero number.
   * @returns ModExpression object.
   */
  mod(first: Expression, second: Expression): ModExpression
  /**
   * Rounds an expression down to the nearest whole number.
   *
   * The `floor()` method configures the aggregation pipeline to round the specified expression down to the nearest integer.
   *
   * @public
   * @documentationMaturity preview
   * @param expression - Expression to round down to the nearest whole number. Expression must resolve to a number. Learn more about using [expressions in aggregation pipelines](https://dev.wix.com/docs/sdk/backend-modules/data/items/wix-data-aggregate-pipeline-expressions/introduction).
   * @returns FloorExpression object.
   */
  floor(expression: Expression): FloorExpression
  /**
   * Rounds an expression up to the nearest whole number.
   *
   * The `ceil()` method configures the aggregation pipeline to round the specified expression up to the nearest integer.
   *
   * @public
   * @documentationMaturity preview
   * @param expression - Expression to round up to the nearest whole number. Expression must resolve to a number. Learn more about using [expressions in aggregation pipelines](https://dev.wix.com/docs/sdk/backend-modules/data/items/wix-data-aggregate-pipeline-expressions/introduction).
   * @returns CeilExpression object.
   */
  ceil(expression: Expression): CeilExpression
  /**
   * Joins multiple expressions together to create a string.
   *
   * The `concat()` method configures the aggregation pipeline to join multiple expressions together to create a new string.
   *
   * @public
   * @documentationMaturity preview
   * @param expressions - Expressions to join together. All expressions must resolve to strings. Learn more about using [expressions in aggregation pipelines](https://dev.wix.com/docs/sdk/backend-modules/data/items/wix-data-aggregate-pipeline-expressions/introduction).
   * @returns ConcatExpression object.
   */
  concat(...expressions: Expression[]): ConcatExpression
  /**
   * Converts an expression to a string.
   *
   * The `stringify()` method configures the aggregation pipeline to convert the specified expression to a string.
   *
   * @public
   * @documentationMaturity preview
   * @param expression - Expression to convert to a string. Learn more about using [expressions in aggregation pipelines](https://dev.wix.com/docs/sdk/backend-modules/data/items/wix-data-aggregate-pipeline-expressions/introduction).
   * @returns StringifyExpression object.
   */
  stringify(expression: Expression): StringifyExpression
  /**
   * Converts an expression to lowercase.
   *
   * The `toLower()` method configures the aggregation pipeline to convert the specified expression to lowercase.
   *
   * @public
   * @documentationMaturity preview
   * @param expression - Expression to convert to lowercase. Expression must resolve to a string. Learn more about using [expressions in aggregation pipelines](https://dev.wix.com/docs/sdk/backend-modules/data/items/wix-data-aggregate-pipeline-expressions/introduction).
   * @returns ToLowerExpression object.
   */
  toLower(expression: Expression): ToLowerExpression
  /**
   * Converts an expression to uppercase.
   *
   * The `toUpper()` method configures the aggregation pipeline to convert the specified expression to uppercase.
   *
   * @public
   * @documentationMaturity preview
   * @param expression - Expression to convert to uppercase. Expression must resolve to a string. Learn more about using [expressions in aggregation pipelines](https://dev.wix.com/docs/sdk/backend-modules/data/items/wix-data-aggregate-pipeline-expressions/introduction).
   * @returns ToUpperExpression object.
   */
  toUpper(expression: Expression): ToUpperExpression
  /**
   * Extracts a portion of a string expression.
   *
   * The `substring()` method configures the aggregation pipeline to extract a portion of the specified string expression.
   *
   * @public
   * @documentationMaturity preview
   * @param expression - Expression to extract a substring from. Expression must resolve to a string. Learn more about using [expressions in aggregation pipelines](https://dev.wix.com/docs/sdk/backend-modules/data/items/wix-data-aggregate-pipeline-expressions/introduction).
   * @param start - Starting position of the substring to extract, specified in zero-based indexing. Expression must resolve to a number.
   * @param length - Number of characters to extract from the starting position. Expression must resolve to a number.
   * @returns SubstringExpression object.
   */
  substring(
    expression: Expression,
    start: Expression,
    length?: Expression
  ): SubstringExpression

  /**
   * Finds the length of a string expression.
   *
   * The `length()` method configures the aggregation pipeline to find the length of the specified expression.
   *
   * @public
   * @documentationMaturity preview
   * @param expression - Expression to find the total number of characters of. Expression must resolve to a string. Learn more about using [expressions in aggregation pipelines](https://dev.wix.com/docs/sdk/backend-modules/data/items/wix-data-aggregate-pipeline-expressions/introduction).
   * @returns LengthExpression object.
   */
  length(expression: Expression): LengthExpression
}
